function jsQuantity() {
    $(document).ready(function () {
        $('.minus-icon').click(function () {
            let quantity = parseInt($('#quantity').val());
            quantity -= 1;

            if (quantity > 0) {
                $('#quantity').val(quantity);
            }
            else {
                $('#quantity').val(parseInt(1));
            }
        });

        $('.plus-icon').click(function () {
            let quantity = parseInt($('#quantity').val());
            quantity += 1;
            $('#quantity').val(quantity);
        });
    });
}
export { jsQuantity };
