function seenProduct() {
    $(document).ready(function () {
        $('.seen-product-slider').owlCarousel({
            margin: 0,
            items: 4,
            navText: ["<a><i class='fas fa-angle-left'></i></a>", "<a><i class='fas fa-angle-right'></i></a>"],
            loop: false,
            rewind: true,
            dots: true,
            nav: true,
            smartSpeed: 1000,
            responsive: {
                0: {
                    items: 2,
                },
                768: {
                    items: 3,
                },
                1200: {
                    items: 4,
                }
            }
        });
    });
}
export { seenProduct };
