function sliderImage() {
  $(document).ready(function () {
    $('.slider-for').slick({
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: true,
      prevArrow: "<div class='prev-arrow'><i class='fas fa-chevron-left'></i></div>",
      nextArrow: "<div class='next-arrow'><i class='fa fa-angle-right'></i></div>",
      asNavFor: '.slider-nav'
    });

    $('.slider-nav').slick({
      slidesToShow: 3,
      slidesToScroll: 1,
      asNavFor: '.slider-for',
      arrows: false,
      dots: true,
      centerMode: true,
      vertical: true,
      focusOnSelect: true
    });

    /**
 * Initiate lightbox 
 */
    const imageLightbox = GLightbox({
      selector: '.image-lightbox',
      openEffect: 'zoom',
      closeEffect: 'fade'
    });

  });
}
export { sliderImage };
