function homeMainSlider(){
    $(document).ready(function(){
        $('.main-slider').owlCarousel({
            margin: 0,
            items: 1,
            loop: false,
            rewind: true,
            dots: true,
            nav: true,
            autoHeight: false,
            smartSpeed: 1000,
        });
    });
}
export {homeMainSlider};
